body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Wrapper {
  box-sizing: border-box;
  background-color: azure;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#drum-machine {
  border: 2px solid blueviolet;
  background-color: blueviolet;
  border-radius: 25px;
  height: 60%;
  min-width: 35em;
  display: flex;
}

#keyboard {
  width: 70%;
  height: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
}
#control {
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.drum-pad {
  justify-content: center;
  align-items: center;

  display: flex;
}
.drum-item {
  border: 1px solid blueviolet;

  border-radius: 25px;
  height: 75%;
  width: 75%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3.5em;
  box-shadow: 2px 2px;
}
#display {
  border: 3px solid rgb(80, 83, 73);
  background-color: rgb(187, 144, 228);
  border-radius: 25px;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  text-align: center;
  margin: 4% 4% 4% 0%;
}
#text {
  height: 70%;
  font-size: 1.3em;
}
@media screen and (max-width: 750px) {
  #drum-machine {
    height: 75%;
  }
}

@media screen and (max-width: 450px) {
  #drum-machine {
    height: 70%;
    min-width: 300px;
  }
  #display {
    font-size: 1.5em;
  }
}
@media screen and (max-height: 400px) {
  #display {
    font-size: 1.5em;
  }
  #text {
    font-size: 1em;
  }
}

